
import {defineComponent} from "vue";
import {GroupData} from "@/store/renderData";
import {store} from "@/store";
import TagContainer from "@/components/v2/TagContainer.vue";

export default defineComponent( {
  name: "TabGroupScaffold",
  components: {TagContainer},
  props: ['title', 'color', 'collapsed', 'countFrames', 'group'],
  data() {
    return {
      newTitle: this.title,
      editTitleMode: false
    }
  },
  methods: {
    addTag(tag: string){
      const newTags = [...this.group.tags]
      newTags.push(tag)

      const groupData: GroupData = {
        title: this.title,
        color: this.color,
        tags: newTags,
        updatedAt: Date.now()
      }
      store.dispatch('upsertSavedGroups', groupData)
    },
    removeTag(tag: string){
      const newTags = [...this.group.tags].filter(x => x !== tag)

      const groupData: GroupData = {
        title: this.title,
        color: this.color,
        tags: newTags,
        updatedAt: Date.now()
      }
      store.dispatch('upsertSavedGroups', groupData)
    },
    collapse(collapsed: boolean){
      // @ts-ignore
      this.port.postMessage({kind: "collapse-tab-groups", group: this.group.id, collapse: collapsed});
    },
    editTitle(){
      this.editTitleMode = true
      this.$nextTick(() => {
        const html = this.$refs.input as HTMLInputElement
        html.focus()
      });
    },
    removeGroup(){
      store.dispatch('removeGroup', this.group.title)
    },
    changeColor(){
      let newColor = this.color
      if (this.color === 'grey'){
        newColor = 'blue'
      }
      if (this.color === 'blue'){
        newColor = 'red'
      }
      if (this.color === 'red'){
        newColor = 'yellow'
      }
      if (this.color === 'yellow'){
        newColor = 'green'
      }
      if (this.color === 'green'){
        newColor = 'pink'
      }
      if (this.color === 'pink'){
        newColor = 'purple'
      }
      if (this.color === 'purple'){
        newColor = 'cyan'
      }
      if (this.color === 'cyan'){
        newColor = 'orange'
      }
      if (this.color === 'orange'){
        newColor = 'grey'
      }

      // @ts-ignore
      this.port.postMessage({kind: "color-tab-groups", group: this.group.id, color: newColor});
    },
    saveNewTitle(){
      if (this.newTitle){
        // @ts-ignore
        this.port.postMessage({kind: "title-tab-groups", group: this.group.id, title: this.newTitle});
      }
      this.editTitleMode = false
    },
  }
})

